
.accountCard {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid black;
    background-color: #fff;
    width: 80%;
    margin: 0 auto;
    flex-direction: column;
    padding: 1.5rem;
    box-sizing: border-box;
    text-align: left;
    margin-bottom: 2rem;
    &__amount {
    margin: 0;
    font-size: 2.5rem;
    font-weight: bold; 
}
&__description {
    margin: 0;
}
&__title {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    font-weight: normal;
}

&__content {
    width: 100%;
    flex: 1;
   
}

&__button {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 1.1rem;
    font-weight: bold;
    margin-top: 1rem;
    border-color: #00bc77;
    background-color: #00bc77;
    color: #fff;
    cursor: pointer;
}
}
@media (min-width: 720px) {
    .accountCard {
        flex-direction: row;
    }

    .accountCard__content {
        flex: 0;
    }

    .accountCard__button {
        width: 200px;
    }
}